import classes from "./Banner.module.scss";
import { useEffect, useRef, useState } from "react";

const pagesList = ["About", "Gallery", "Recordings", "Contact"];
const picturesList = ["Banner1", "Banner2", "Banner3", "Banner1"];

function Banner({ isMobile, page }) {
  const [offSet, setOffSet] = useState(0);
  const [image, setImage] = useState("");
  const [header, setHeader] = useState("");
  const imageRef = useRef();
  const headerRef = useRef();
  useEffect(() => {
    headerRef.current.style.opacity = 0;
    setTimeout(() => {
      imageRef.current.style.opacity = 0;
    }, 200);

    setTimeout(() => {
      setImage(picturesList[page - 1]);
      imageRef.current.style.opacity = 1;
      setTimeout(() => {
        setHeader(pagesList[page - 1]);
        headerRef.current.style.opacity = 0.7;
      }, 500);
    }, 700);
  }, [page]);

  useEffect(() => {
    window.onscroll = () => {
      setOffSet(Math.floor(window.pageYOffset));
    };
  }, []);

  return (
    <div
      className={classes.backgroundImage}
      ref={imageRef}
      style={{
        backgroundImage: "url(/pictures/" + image + ".jpg)",
        backgroundPositionY: (offSet / 400) * 40 + "%",
        backgroundPositionX: isMobile ? "40%" : 0,
      }}
    >
      <div className={classes.header} ref={headerRef}>
        {header}
      </div>
    </div>
  );
}
export default Banner;
