import classes from "./landingPage.module.scss";
import { Route, Routes } from "react-router-dom";
import About from "../About/About";
import Recordings from "../Recordings/Recordings";
import ContactForm from "../ContactForm/ContactForm";
import Navigation from "../Navigation/Navigation";
import Home from "../Home/Home";
import Gallery from "../Gallery/Gallery";
import { useEffect, useState } from "react";
import Contact from "../Contact/Contact";
import Banner from "../Banner/Banner";

function LandingPage() {
  const [mouseClicked, setMouseClicked] = useState(false);
  const [isMobile, setIsMobile] = useState(
    window.innerWidth < 1000 ? true : false
  );
  const [openCollapse, setOpenCollapse] = useState(false);
  const [page, setPage] = useState(0);
  function closeCollapse() {
    if (openCollapse) {
      setOpenCollapse(false);
    }
  }

  useEffect(() => {
    window.onresize = () => {
      window.innerWidth < 1000 ? setIsMobile(true) : setIsMobile(false);
    };
  }, []);

  let showBanner =
    page !== 0 ? <Banner isMobile={isMobile} page={page} /> : null;

  return (
    <div className={classes.wrapper} onClick={closeCollapse}>
      <Navigation
        mouseClicked={mouseClicked}
        isMobile={isMobile}
        setOpenCollapse={setOpenCollapse}
        openCollapse={openCollapse}
        page={page}
        setPage={setPage}
      />
      {showBanner}

      <Routes>
        <Route
          path="/"
          element={
            <Home
              mouseClicked={mouseClicked}
              isMobile={isMobile}
              setMouseClicked={setMouseClicked}

            />
          }
        ></Route>
        <Route path="/About" element={<About isMobile={isMobile} setPage={setPage} />}></Route>
        <Route
          path="/Recordings"
          element={<Recordings isMobile={isMobile} setPage={setPage} />}
        ></Route>
        <Route
          path="/Gallery"
          element={<Gallery isMobile={isMobile} setPage={setPage}/>}
        ></Route>
        <Route
          path="/Contact"
          element={<ContactForm isMobile={isMobile} setPage={setPage} setMouseClicked={setMouseClicked}/>}
        ></Route>
      </Routes>
      <Contact isMobile={isMobile} />
    </div>
  );
}

export default LandingPage;
