import emailjs from "emailjs-com";
import { Formik, Form } from "formik";
import InputField from "./InputField";
import classes from "./ContactForm.module.scss";
import { useEffect } from "react";

function ContactForm({ isMobile, setPage, setMouseClicked }) {
  let formLayout = isMobile
    ? `${classes.mobileInputNames}`
    : `${classes.inputNames}`;

  let rowWidth = isMobile ? 100 : 47.5;

  useEffect(()=>{
    setPage(4)
    setMouseClicked(true)
  },[])

  const sendEmail = (e) => {
    // e.preventDefault();

    emailjs
      .send("service_lkjzip4", "contact_form", e, "user_uPCSdjMhNV2TzZ8CQgxYr")
      .then(
        (result) => {
          console.log(result.text);
        },
        (error) => {
          console.log(error.text);
        }
      );
    
  };
  return (
    <div className={classes.home}>
      <Formik
        initialValues={{ firstName: "", lastName: "", email: "", message: "" }}
        validate={(values) => {
          const errors = {};
          if (!values.email) {
            errors.email = "Required";
          } else if (
            !/^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,}$/i.test(values.email)
          ) {
            errors.email = "Invalid email address";
          }
          if (!values.firstName) {
            errors.firstName = "Required";
          }
          if (!values.lastName) {
            errors.lastName = "Required";
          }
          return errors;
        }}
        onSubmit={(values, { setSubmitting, resetForm }) => {
          sendEmail(values);
          setTimeout(() => {
            alert(`Hello ${values.firstName}! thanks for your mail :)!` )
            setSubmitting(false);
          }, 400);
          resetForm();
        }}
      >
        {({ isSubmitting }) => (
          <Form
            className={classes.wrapper}
            style={{ width: isMobile ? "85%" : "50%" }}
            onScroll={Formik.handleSubmit}
          >
            <div className={formLayout}>
              <InputField
                name="firstName"
                title="First Name"
                width={rowWidth}
                height={50}
                placeholder="First Name"
                type="input"
              />
              <InputField
                name="lastName"
                title="Last Name"
                width={rowWidth}
                height={50}
                placeholder="Last Name"
                type="input"
              />
            </div>
            <InputField
              name="email"
              title="Email"
              width={100}
              height={50}
              placeholder="Email"
              type="input"
            />
            <InputField
              name="mesagge"
              title="Message"
              width={100}
              height={200}
              type="textarea"
              placeholder="write your message here!"
            />
            <button
              className={classes.button}
              type="submit"
              // disabled={isSubmitting}
            >
              Submit
            </button>
          </Form>
        )}
      </Formik>
    </div>
  );
}

export default ContactForm;

